body {
  font:13px/22px  "Helvetica Neue",Helvetica,"Noto Sans","ff-tisa-web-pro-1","ff-tisa-web-pro-2","Lucida Grande","Hiragino Sans GB", "Microsoft YaHei", "WenQuanYi Micro Hei", sans-serif;
  -webkit-font-smoothing: antialiased;
  color:#333;
}

.inline-block {display: inline-block;}
.hidden {display: none;}

/* logo */
.logo h1 {color: #fff; margin: 0;}

/*  */
.page-container .sidebar-menu {/*position: fixed;*/top:0;}

/* overwrite */
.form-horizontal .form-group {
    padding-bottom: 5px;
    padding-top: 5px;
    margin:0;
}
.breadcrumb {margin-bottom: 0px;}

/* tips */
div.tips {
  margin: 2px 0;
  padding: 8px 20px;
  border-left: 3px solid #eeeeee;
}
div.tips a.pull-right {
  font-size: 12px;
  margin-right: -10px;
  line-height: 20px;
}
div.tips ul {
    margin-left: -30px;
    margin-bottom: 0;
}

div.tips i {
    float: right !important;
    font-style: normal;
    cursor:default;
}

div.tips h4 {
  margin-top: 0;
  margin-bottom: 5px;
}
div.tips p:last-child {
  margin-bottom: 0;
}
div.text-danger {
  border-color: #eed3d7;
  background-color: #fdf7f7;
}
div.text-danger h4 {
  color: #b94a48;
}
div.text-warning {
  border-color: #faebcc;
  background-color: #faf8f0;
}
div.text-warning h4 {
  color: #8a6d3b;
}
div.text-info {
  border-color: #bce8f1;
  background-color: #f4f8fa;
}
div.text-info h4 {
  color: #34789a;
}

