.do {width:20%; min-width: 230px;}

/*  */
.overlayout {
  position: absolute;
  z-index: 9999999999;
  height: 100%;
  width: 100%;
}

.overlayout .black-layout {
  position: absolute;
  top:0;
  left:0;
  bottom:0;
  background: #333;
  opacity: 0.3;
  height: 100%;
  width: 100%;
}

.overlayout .progress {
  position: fixed;
  top:50%;
  left:50%;
  width: 500px;
  margin-left: -250px;
}

