/* Tips  */
.tips{}
.tips-angle{position:absolute;display:block;width:8px;height:8px;font-size:0;background:#fff;border-left:1px solid #cfcfcf;border-top:1px solid #cfcfcf;top:-5px;top:-6px9;left:10px;}

table tr td .td-tool-bar-wrapper{position: relative;}
table tr td .td-tool-bar {display: none;position: absolute;top:10px; left:30%;background: #fff; border:1px solid #cfcfcf;padding:10px; min-width: 170px; box-shadow: #ccc 0 0 5px;}
table tr td:hover .td-tool-bar {display: block;}

